import * as React from 'react';
import Typography from '@mui/material/Typography';
import {
    AppBar,
    BottomNavigation,
    BottomNavigationAction,
    Fab,
    Grid,
    Paper,
    Toolbar, Tooltip
} from "@mui/material";
import {
    PlayArrowRounded,
    PauseRounded,
    StopRounded,
    ReplyRounded,
    Upload,
    Done
} from '@mui/icons-material';
import Board from "./board";
import {useEffect, useState} from "react";
import RobotWindow from "./RobotCode/RobotWindow";
import {calculateTimeRemaining} from './functions';
import {useSnackbar} from 'notistack';

export default function App() {

    const {enqueueSnackbar} = useSnackbar();

    const [robot, setRobot] = useState(null)
    const [timer, setTimer] = useState('')
    const [score, setScore] = useState(0)
    const [battery, setBattery] = useState(100)
    const [name, setName] = useState('TEAM_NAME');

    const [loadEnabled, setLoadEnabled] = useState(true)
    const [unloadEnabled, setUnloadEnabled] = useState(true)
    const [runEnabled, setRunEnabled] = useState(true)
    const [pauseEnabled, setPauseEnabled] = useState(true)
    const [giveUpEnabled, setGiveUpEnabled] = useState(true)
    const [lopEnabled, setLopEnabled] = useState(true)

    const [log, setLog] = useState('');

    const [loaded, setLoaded] = useState(false);

    function preRun() {
        setLoadEnabled(false);
        setUnloadEnabled(false);
        setRunEnabled(false);
        setPauseEnabled(true);
        setGiveUpEnabled(true);
        setLopEnabled(true);
    }

    function startup() {
        setLog('');
        setLoaded(false);

        setRunEnabled(true);
        setPauseEnabled(false);
        setLopEnabled(false);
        setLoadEnabled(true);
        setUnloadEnabled(true);
        setGiveUpEnabled(false);
    }

    function endGame() {
        setRunEnabled(false);
        setPauseEnabled(false);
        setLopEnabled(false);
    }

    function update(data) {
        setScore(data[0])
        setBattery(data[4])

        let maxTime = 8 * 60;
        if (data[2])
            maxTime = data[2];
        maxTime = parseInt(maxTime);
        setTimer(calculateTimeRemaining(data[1], maxTime));
    }

    function giveUp() {
        robot.send('quit');
        setRunEnabled(false);
        setPauseEnabled(false);
        setLopEnabled(false);
        setGiveUpEnabled(false);
    }

    function fileOpened(filesId, acceptTypes, location) {
        const files = document.getElementById(filesId).files;
        if (files.length > 0) {
            const file = files[0];

            const nameParts = file.name.split(".");

            if (nameParts.length >= 1) {
                if (nameParts.length === 1 || acceptTypes.indexOf(nameParts[nameParts.length - 1]) !== -1) {
                    const fd = new FormData();
                    for (let i = 0; i < files.length; i++) {
                        const f = files[i];
                        fd.append(`file${(i + 1)}`, f, f.name);
                    }

                    let xmlhttp = new XMLHttpRequest();
                    xmlhttp.onreadystatechange = function () {
                        if (xmlhttp.readyState === 4 && xmlhttp.status !== 200) {
                            console.log(xmlhttp.status);
                            enqueueSnackbar(xmlhttp.responseText ?? "an error occured", {
                                variant: 'error',
                                anchorOrigin: {vertical: "bottom", horizontal: "center"},
                                autoHideDuration: 4000
                            })
                        }
                        if (xmlhttp.readyState === 4 && xmlhttp.status === 200) {
                            enqueueSnackbar('code loaded', {
                                variant: 'success',
                                anchorOrigin: {vertical: "bottom", horizontal: "center"},
                                autoHideDuration: 4000
                            })
                            setLoaded(true);
                        }
                    };
                    xmlhttp.open("POST", "http://127.0.0.1:6975/" + location + "/", true);
                    xmlhttp.send(fd);
                } else {
                    enqueueSnackbar("Please select a controller with a valid file type from: .py, .exe, .class, .jar, .bsg, .m or no extension (for Linux/Mac users)", {
                        variant: "warning",
                        anchorOrigin: {vertical: "bottom", horizontal: "center"},
                        autoHideDuration: 4000
                    })
                }
            } else {
                enqueueSnackbar("Please select a controller program", {
                    variant: "warning",
                    anchorOrigin: {vertical: "bottom", horizontal: "center"},
                    autoHideDuration: 4000
                })
            }

        } else
            enqueueSnackbar("No file found",
                {
                    variant: 'error',
                    autoHideDuration: 4000,
                    anchorOrigin: {vertical: 'bottom', horizontal: 'center'}
                }
            )
    }

    function receive(message) {
        var parts = message.split(",");
        console.log(parts);
        if (parts.length > 0) {
            switch (parts[0]) {
                case "startup":
                    startup();
                    break;
                case "update":
                    update(parts.slice(1, parts.length + 1));
                    break;
                case "config":
                    break;
                case "unloaded":
                    // TODO: write
                    unloadedController();
                    break;
                case "loaded":
                    // TODO: write
                    loadedController();
                    break;
                case "ended":
                    endGame();
                    break;
                case "historyUpdate":
                    // TODO: write
                    let history0 = message.split(",").slice(1, message.length - 1)
                    // updateHistory(history0)
                    console.log(history0);
                    break;
                case "setName":
                    let name = message.split(",").slice(1, message.length - 1)
                    setName(name);
                    break;
                case "loadControllerPressed":
                    // TODO: write
                    openLoadController();
                    break;
                case "unloadControllerPressed":
                    // TODO: write
                    unloadedController();
                    break;
                case "pausedPressed":
                    // TODO: write
                    pausePressed();
                    break;
            }
        }
    }

    useEffect(() => {
        setRobot(new RobotWindow());
    }, []);

    useEffect(() => {
        if (robot === null) return;
        robot.setTitle('Fira Smart Home');
        setTimer('Initializing');
        robot.receive = receive;
        robot.send("rw_reload");
        setScore(0)
        setTimer('08:00');
    }, [robot]);

    return (
        <div style={{flexGrow: 1}}>
            <AppBar position="static">
                <Toolbar>
                    <Typography variant="h6" style={{flexGrow: 1, textAlign: 'center'}}>
                        FIRA Smart Home {new Date().getFullYear()}
                    </Typography>
                </Toolbar>
            </AppBar>
            <Grid container spacing={3} style={{padding: 20}}>
                <Grid item xs={12}>
                    <Paper style={{padding: 20, textAlign: 'center'}}>
                        <Board timer={timer} score={score} battery={battery} name={name}/>
                    </Paper>
                </Grid>
                <Grid item xs={12} style={{textAlign: 'center'}}>
                    <Paper style={{padding: 20}}>
                        {/* Placeholder for simulation area */}
                        <div style={{height: 400, backgroundColor: '#f5f5f5'}}></div>
                    </Paper>
                </Grid>
            </Grid>
            <Paper sx={{position: 'fixed', bottom: 0, left: 0, right: 0}} elevation={3}>
                <BottomNavigation
                    showLabels
                    onChange={(_, newValue) => {
                        switch (newValue) {
                            // Play
                            case 0:
                                preRun();
                                robot.send('run')
                                break;
                            // Stop
                            case 1:
                                console.log('quit pressed')
                                robot.send("quit");
                                break;
                            // Rolocation
                            case 2:
                                console.log('relocate pressed')
                                robot.send("relocate");
                                break;
                        }
                    }}
                >
                    <BottomNavigationAction label="Play" icon={<PlayArrowRounded/>}/>
                    {/*<BottomNavigationAction label="Pause" icon={<PauseRounded/>}/>*/}
                    <BottomNavigationAction label="Stop" icon={<StopRounded/>}/>
                    <BottomNavigationAction label="Replay" icon={<ReplyRounded/>}/>
                </BottomNavigation>
            </Paper>
            <input type="file" style={{display: 'none'}} accept=".py"
                   onChange={() => {
                       fileOpened('robotCode', '[\'py\']', 'controllers/robotCode')
                   }}
                   id="robotCode"/>
            <Tooltip title={"Load Code"} arrow>
                <Fab color="secondary" sx={{position: 'absolute', bottom: 70, right: 16}}
                     onClick={() => {
                         if (loaded) {
                             robot.send("robotUnload");
                             robot.send("unloadControllerPressed");
                             setLoaded(false);
                         } else {
                             document.getElementById("robotCode").click();
                             robot.send("loadControllerPressed");
                         }
                     }}>
                    {loaded ? <Done/> : <Upload/>}
                </Fab>
            </Tooltip>
        </div>
    );
}
