import * as React from 'react';
import Typography from '@mui/material/Typography';
import {Grid} from "@mui/material";
import {
    Schedule,
    Score,
    BatteryFullRounded,
    Battery3BarRounded,
    Battery4BarRounded,
    Battery6BarRounded,
    Battery2BarRounded,
    Battery1BarRounded,
    Battery0BarRounded
} from '@mui/icons-material';
import { green, red } from '@mui/material/colors';

export default function Board({timer, score, battery, name}) {

    function getBatteryImage() {
        if(battery == 100)
            return <BatteryFullRounded sx={{ color: green[600] }}/>;
        if(battery > 80)
            return <Battery6BarRounded sx={{ color: green[600] }}/>;
        if(battery > 60)
            return <Battery4BarRounded/>;
        if(battery > 40)
            return <Battery3BarRounded/>;
        if(battery > 20)
            return <Battery2BarRounded/>;
        if(battery > 0)
            return <Battery1BarRounded sx={{ color: red[500] }}/>;
        if(battery == 0)
            return <Battery0BarRounded/>;
        return <BatteryFullRounded/>;
    }

    return (
        <>
            <Typography xs={12} variant="h6" style={{flexGrow: 1, textAlign: 'center'}}>{name}</Typography>
            <Grid container spacing={3} display={{xs: 'none', md: 'flex'}} alignItems='center'>
                <Grid item xs={3} display={{xs: 'none', md: 'block'}}>
                    <Typography variant="h6">{timer}</Typography>
                    <Schedule/>
                </Grid>
                <Grid item xs={6} flexDirection='column'>
                    <Typography variant="h3">{score}</Typography>
                    <Score/>
                </Grid>
                <Grid item xs={3}>
                    <Typography variant="h6">{battery}%</Typography>
                    {getBatteryImage()}
                </Grid>
            </Grid>
            <Grid container spacing={3} display={{xs: 'flex', md: 'none'}}>
                <Grid item xs={12}>
                    <Typography variant="h3">{score}</Typography>
                    <Score/>
                </Grid>
                <Grid item xs={6}>
                    <Typography variant="h6">{timer}</Typography>
                    <Schedule/>
                </Grid>
                <Grid item xs={6}>
                    <Typography variant="h6">{battery}%</Typography>
                    {getBatteryImage()}
                </Grid>
            </Grid>
        </>
    );
}